import React from "react";
import {
   CardBody,
   CardHeader,
   Card,
   Table,
   Row,
   Col,
   FormGroup,
   Label,
   Input,
   CardTitle,
} from "reactstrap";
import { connect } from "react-redux";
import PlaceCart from "./PlaceCart";
import Paymentb from "./paymentb";
import backImg from "../../imgs/demo.jpeg";

//material-ui
import Button from "@material-ui/core/Button";
const styles = {
   row: {
      backgroundImage: `url(${backImg})`,
      height: "130vh",
      backgroundRepeat: "no-repeat",
      backgroundSize: "cover",
   },
   content: {
      height: "90%",
      color: "white",
      backgroundColor: "rgba(0, 3, 0, 0.5)",
   },
};
class Place extends React.Component {
   constructor(props) {
      super(props);
      this.state = {
         userInfo: this.props.userInfo,
         cartPrice: 0,
         paymentData: {},
      };
   }

   cartTotal = (price) => {
      this.setState({ cartPrice: price });
   };
   paymentData = (data) => {
      this.setState({ paymentData: data });
   };
   render() {
      const { userInfo, paymentData } = this.state;

      return (
         <Row style={styles.row}>
            <Col md="6" sm="12">
               <Card style={styles.content}>
                  <CardHeader>
                     <div className="d-flex justify-content-between">
                        <CardTitle tag="h5">User Information</CardTitle>
                        <div className="d-flex flex-wrap flot-right">
                           <Button
                              style={{
                                 color: "#000",
                              }}
                              color="secondary"
                              type="submit"
                              variant="contained"
                              onClick={() => this.props.history.push("/")}
                              outline
                           >
                              <span className="align-middle">Back To Menu</span>
                           </Button>
                        </div>
                     </div>
                  </CardHeader>
                  <CardBody>
                     {userInfo.name ? (
                        <Row>
                           <Col sm="12">
                              <FormGroup className="form-label-group position-relative has-icon-left">
                                 <Label>Name</Label>
                                 <Input
                                    type="text"
                                    name="name"
                                    readOnly
                                    value={
                                       userInfo && userInfo.name
                                          ? userInfo.name
                                          : "loading..."
                                    }
                                 />
                              </FormGroup>
                           </Col>

                           <Col sm="12">
                              <FormGroup className="form-label-group position-relative has-icon-left">
                                 <Label>E-mail</Label>
                                 <Input
                                    type="email"
                                    name="email"
                                    readOnly
                                    value={
                                       userInfo && userInfo.email
                                          ? userInfo.email
                                          : "loading..."
                                    }
                                 />
                              </FormGroup>
                           </Col>
                        </Row>
                     ) : (
                        <Row>
                           <Col md="6" sm="12">
                              <FormGroup className="form-label-group position-relative has-icon-left">
                                 <Label>First Name</Label>
                                 <Input
                                    type="text"
                                    name="firstName"
                                    readOnly
                                    value={
                                       userInfo && userInfo.firstName
                                          ? userInfo.firstName
                                          : "loading..."
                                    }
                                 />
                              </FormGroup>
                           </Col>
                           <Col md="6" sm="12">
                              <FormGroup className="form-label-group position-relative has-icon-left">
                                 <Label>Last Name</Label>
                                 <Input
                                    type="text"
                                    name="lastName"
                                    readOnly
                                    value={
                                       userInfo && userInfo.lastName
                                          ? userInfo.lastName
                                          : "loading..."
                                    }
                                 />
                              </FormGroup>
                           </Col>
                           <Col md="6" sm="12">
                              <FormGroup className="form-label-group position-relative has-icon-left">
                                 <Label>E-mail</Label>
                                 <Input
                                    type="email"
                                    name="email"
                                    readOnly
                                    value={
                                       userInfo && userInfo.email
                                          ? userInfo.email
                                          : "loading..."
                                    }
                                 />
                              </FormGroup>
                           </Col>
                           <Col md="6" sm="12">
                              <FormGroup className="form-label-group position-relative has-icon-left">
                                 <Label>PinCode</Label>
                                 <Input
                                    type="number"
                                    name="pinCode"
                                    readOnly
                                    value={
                                       userInfo && userInfo.pinCode
                                          ? userInfo.pinCode
                                          : "loading..."
                                    }
                                 />
                              </FormGroup>
                           </Col>
                           <Col sm="12">
                              <FormGroup className="form-label-group position-relative has-icon-left">
                                 <Label>Address</Label>
                                 <Input
                                    type="textarea"
                                    name="address"
                                    readOnly
                                    value={
                                       userInfo && userInfo.address
                                          ? userInfo.address
                                          : "loading..."
                                    }
                                 />
                              </FormGroup>
                           </Col>
                        </Row>
                     )}
                  </CardBody>
               </Card>
            </Col>

            <Col md="6" sm="12">
               <PlaceCart
                  style={styles.content}
                  cartData={this.props.cartItem}
                  cartTotal={this.cartTotal}
               />
            </Col>

            <Col md="6" sm="12">
               <Paymentb
                  dispatch={this.props.dispatch}
                  userData={userInfo}
                  data={this.paymentData}
                  cartPrice={this.state.cartPrice}
               />
            </Col>
            <Col md="6" sm="12">
               <Table style={styles.content} responsive bordered>
                  <thead>
                     <tr>
                        <th>Transaction Id</th>
                        <th>Amount</th>
                        <th>Status</th>
                     </tr>
                  </thead>

                  <tbody>
                     <tr>
                        <td>{paymentData.transaction}</td>

                        <td>{paymentData.amount}&nbsp; INR</td>

                        <td>{paymentData.status}</td>
                     </tr>
                  </tbody>
               </Table>
            </Col>
         </Row>
      );
   }
}
//Once data are store in dispatch variable so whenever use it variable to call like this in below:
const mapStateToProps = (state) => {
   return {
      cartItem: state.globalReducer.data,
      userInfo: state.globalReducer.userInfo,
   };
};
export default connect(mapStateToProps)(Place);
